:root {
  --blue-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header_link {
  width: 40px;
  height: 40px;
  display: block;
}

.logo {
  width: 100%;
  height: 100%;
}

.nav {
  align-items: center;
  display: block;
}

.nav_list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-li {
  margin-left: 20px;
}

.nav-li:first-child {
  margin-left: 0;
}

.nav-link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .5s;
  display: flex;
  position: relative;
}

.nav-link:hover {
  color: var(--blue-color);
}

.nav-link.is-active {
  color: var(--blue-color);
  position: relative;
}

.nav-link.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
